body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image:url("/bckg-city.png");
  background-repeat:no-repeat;
  background-size:contain;
  background-color:#020937;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Container {
  font-size: 12px;
}


.title {
  font-family: Rajdhani;
  color: #FFFFFF; }

.About {
  position: relative;
  display: flex;
  justify-content: center;
  margin: auto;
  margin-bottom: 10%;
  text-align: left;
  text-align: initial;
  color: white;
}
/* info container */
.About > .info-container {
  display: inline-block;
  min-width: 25%; max-width: 25%;
}
.About > .info-container > .title {
  display: inline-block;
}
.About > .info-container > p {
  display: inline-block;
  margin-top: 1rem;
  font-size: 120%;
  text-align: left;
}
/* image container */
.About > .image-container {
  position: relative;
  display: inline-block;
  min-width: 35%; max-width: 35%;
  margin-left: 1rem;
}
.About > .image-container > img.main {
  min-width: 100%; max-width: 100%;
  min-height: 100%;
  -webkit-clip-path: polygon(23% 0, 100% 0, 100% 100%, 0 100%, 28% 56%, 5% 26%);
  clip-path: polygon(25% 0, 100% 0, 100% 100%, 0 100%, 28% 55%, 8% 26%);
}
.About > .image-container > img.icon {
  position: absolute;
  z-index: 1;
}
.About > .image-container > img.triangle {
  top: 34.5%; left: 10%;
}
.About > .image-container > img.spark {
  top: -25%; right: -10%;
}

@media (max-width: 768px) {
  .About {
    display: block;
  }
  .About > .info-container, .About > .image-container {
    display: block;
    min-width: 90%;
    margin: auto;
    margin-bottom: 3rem;
  }
  .About > .image-container > img.triangle {
    top: 28%; left: 1rem;
    max-width: 120px;
  }
  .About > .image-container > img.spark {
    top: -40px; right: -10px;
    max-width: 120px;
  }
}

.button {
  display: inline-block;
  text-transform: uppercase;
  vertical-align: top;
  font-weight: bold;
  padding-top: 10px;
  padding-bottom: 10px;
  border: none;
  border-top: 2px solid transparent;
  border-bottom: 2px solid transparent;
  font-size: 0.8em;
  background-color: transparent;
  cursor: pointer;
  background-size: contain;
  background-repeat: round no-repeat;
  color: black;
  width: 12em;
  text-align: center;
  margin-right: 0px; }
  .button.pink {
    background-image: url("/button-bckg-pink.png"); }
  .button.orange {
    background-image: url("/button-bckg-orange.png"); }
  .button:hover {
    background-image: url("/button-bckg-blue.png");
    border-bottom-color: transparent; }

.Location {
  margin: 150px auto;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: relative;
  font-size: 1.5em;
}

.map {
  margin: 50px 0;
  width: 80vw;
  height: calc(80vw / 1.7);
  max-width: 960px;
  max-height: 540px;
}

.Location > img {
  position: absolute;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 1350px;
  z-index: -1;
  transition: 0.1s;
}

@media only screen and (max-width: 1350px) {
  .Location > img {
    width: 110vw;
    left: -50px;

  }
}
@media only screen and (max-width: 500px) {
  .Location {
    margin: 100px 0;
  }
  .map {
    margin: 30px 0;
    width: 90vw;
    height: calc(90vw / 1.4);
  }
  .Location > img {
    width: 500px;
    left: -50px;
}
}

.Team {
  color: white;
  font-family: AktivGrotesk, sans-serif; }
  .Team .title {
    font-size: 3em; }
  .Team .List {
    display: flex;
    justify-content: center;
    list-style: none;
    padding: 0; }
    .Team .List .Member {
      width: 250px;
      position: relative; }
      .Team .List .Member:hover .Splash {
        -webkit-transform: scale(1);
                transform: scale(1); }
      .Team .List .Member:nth-child(2) .Splash {
        background-position-y: -60px; }
      .Team .List .Member .Splash {
        position: absolute;
        z-index: -1;
        width: 250px;
        height: 250px;
        -webkit-transform: scale(0);
                transform: scale(0);
        top: 0;
        right: 0;
        left: 0;
        margin: auto;
        background-repeat: no-repeat;
        transition: 0.2s cubic-bezier(0.165, 0.84, 0.44, 1);
        background-position-x: center; }
      .Team .List .Member img {
        border-radius: 100%;
        object-fit: cover;
        margin-top: 50px;
        width: 170px;
        height: 170px;
        -webkit-user-select: none;
           -moz-user-select: none;
            -ms-user-select: none;
                user-select: none; }
      .Team .List .Member .Name {
        font-size: 30px;
        line-height: 1.3;
        margin: 20px auto;
        width: 200px; }
      .Team .List .Member .Position {
        font-size: 12px;
        width: 200px;
        margin: auto; }
      @media screen and (max-width: 1200px) {
        .Team .List .Member {
          width: 200px;
          margin: 0 5px; }
          .Team .List .Member .Name,
          .Team .List .Member .Position {
            width: inherit; }
          .Team .List .Member img {
            width: 160px;
            height: 160px; } }
      @media screen and (max-width: 1050px) {
        .Team .List .Member {
          width: 160px; }
          .Team .List .Member:hover .Splash {
            -webkit-transform: scale(0.8);
                    transform: scale(0.8); }
          .Team .List .Member .Splash {
            left: -20%; }
          .Team .List .Member img {
            width: 140px;
            height: 140px; } }
      @media screen and (max-width: 840px) {
        .Team .List .Member {
          width: 140px; }
          .Team .List .Member .Name {
            font-size: 25px; }
          .Team .List .Member .Splash {
            left: -35%;
            top: -5%; }
          .Team .List .Member img {
            width: 120px;
            height: 120px; } }
      @media screen and (max-width: 730px) {
        .Team .List .Member {
          width: 120px; }
          .Team .List .Member .Name {
            font-size: 20px;
            margin-top: 10px; }
          .Team .List .Member .Position {
            font-size: 11px; }
          .Team .List .Member:hover .Splash {
            -webkit-transform: scale(0.7);
                    transform: scale(0.7); }
          .Team .List .Member .Splash {
            left: -45%;
            top: -10%; }
          .Team .List .Member img {
            width: 100px;
            height: 100px; } }
      @media screen and (max-width: 630px) {
        .Team .List .Member {
          width: 100%;
          margin-bottom: 20px; }
          .Team .List .Member .Name {
            font-size: 30px; }
          .Team .List .Member .Position {
            font-size: 14px; }
          .Team .List .Member:hover .Splash {
            -webkit-transform: scale(1);
                    transform: scale(1); }
          .Team .List .Member .Splash {
            left: 0;
            top: 0; }
          .Team .List .Member img {
            width: 180px;
            height: 180px; } }
    @media screen and (max-width: 630px) {
      .Team .List {
        flex-direction: column;
        align-items: center; } }

.Header {
  display: flex;
  align-items: center;
  width: 60%;
  min-width: 600px;
  max-width: 1200px;
  margin:auto;
  margin-top: 3%;
  text-align: left;
  text-align: initial;
}

.Header .Logo {
  display: inline-block;
  width:10%;
  margin-right: 15px;
}

.Header .Navigation {
  display: inline-block;
  vertical-align: top;
  text-align: left;
  text-align: initial;
}

.Header .Navigation a {
  display: inline-block;
  text-transform: uppercase;
  color: white;
  text-decoration: none;
  vertical-align: top;
  font-weight: bold;
  padding-top: 10px;
  padding-bottom: 10px;
  border-top: 2px solid transparent;
  border-bottom: 2px solid transparent;
  font-size: 0.8em;
  margin-right: 20px;
}

.Header nav a:hover {
  border-bottom-color: white;
}

.Intro {
  display: inline-block;
  margin: auto;
  margin-top: 10%;
  margin-bottom: 40%;
  position: relative;
  text-align: left;
  text-align: initial;
}

.Intro span {
  color: #00ded3;
  font-size: 1.5em;
  font-weight: bold;
  position: absolute;
  left: 0;
  margin-bottom: 10px;
}

.Intro span:last-of-type {
  left: unset;
  right: 0;
}

.Intro h1 {
  margin: 0px;
  color: #ffffff;
  font-size: 4em;
  font-weight: bold;
  padding-top: 0.6em;
}

.Footer {
  background-image: url("/planet-react.png");
  background-repeat: no-repeat;
  background-position-x: center;
  background-position-y: 25px;
  background-size: cover;
  color: white;
  height: 800px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end; }
  .Footer .Title {
    font-size: 20px;
    width: 200px;
    margin: auto;
    margin: 0 auto 50px auto;
    text-align: center;
    font-size: 2em; }
  .Footer .Link {
    color: #00ded3; }
  .Footer .Icons {
    list-style: none;
    padding: 0;
    display: flex;
    justify-content: center;
    margin-bottom: 30px; }
  .Footer .Icon {
    margin: 0 15px; }
  .Footer .Icon svg {
    fill: white;
    transition: 0.2s;
    width: 35px;
    height: 35px; }
  .Footer .Icon:hover svg {
    fill: #d91dbd; }
  .Footer .PressRelease {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
    font-weight: 500;
    font-size: 1.2em; }
  .Footer .PressRelease a {
    margin: 0 5px;
    font-size: 1.1em; }
  .Footer .Copyright {
    background: #000318;
    font-size: .9em; }
  .Footer .Copyright .Link {
    margin-left: 5px; }
  .Footer .Copyright .Link:nth-child(3) {
    margin-right: 5px; }
  .Footer .Copyright p {
    margin: 10px 20px 30px; }
  @media only screen and (max-width: 560px) {
    .Footer {
      background-position-y: 100%;
      background-size: 1200px;
      height: 757px; }
      .Footer .Title {
        font-size: 32px; }
      .Footer .Icon svg {
        width: 30px;
        height: 30px; }
      .Footer .Copyright p {
        font-size: 14px; } }
  @media only screen and (max-width: 330px) {
    .Footer {
      background-position-y: 100%;
      background-size: 1000px;
      height: 630px; }
      .Footer .Title {
        font-size: 30px; }
      .Footer .Icon svg {
        width: 20px;
        height: 20px; }
      .Footer .Copyright p {
        margin: 10px 25px 30px; } }

